export function isEmpty(value) {
    if (value === null || value === undefined || value === '') {
      return true;
    }
    if (Array.isArray(value) && value.length === 0) {
      return true;
    }
    if (typeof value === 'object' && Object.keys(value).length === 0) {
      return true;
    }
    return false;
  }

  export const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  
    if (!emailRegex.test(email)) {
      return false;
    }
    return true; // Email is valid
  };

  export function generateYearListDesc() {
    const currentYear = new Date().getFullYear(); // 
    const yearList = [];
    for (let year = currentYear; year >= 1980; year--) {
      yearList.push(year);
    }
    return yearList;
  }

  export function generatePriceList() {
    const priceList = [];
    for (let i = 1; i <= 100; i++) {
      const priceValue = i * 100000; // 
      priceList.push({
        name_ja: `${i * 10}`,     // 
        name_en: `${i} million JPY`, // 
        code: `${priceValue}`        // 
      });
    }
    return priceList;
  };

  export function formatToYen(amount) {
    if (typeof amount !== "string" || isNaN(Number(amount))) {
      throw new Error("Input must be a valid numeric string");
    }
    const number = Number(amount);
    return `${number.toLocaleString("en-US")}`;
  }


  // 
export function formatYearMonth(value, locale) {
  if (!value) return '';
  if (locale === 'ja') {
    return `${value.slice(0, 4)}${value.slice(4, 6)}`;
  } else {
    const date = new Date(value.slice(0, 4), value.slice(4, 6) - 1);
    return date.toLocaleString('en-US', { year: 'numeric', month: 'long' });
  }
}

// 
export function formatMileage(value) {
  if (!value) return '';
  return `${Number(value).toLocaleString()}km`;
}

// 
export function formatDisplacement(value) {
  if (!value) return '';
  return `${Number(value).toLocaleString()}cc`;
}

// 
export function formatYearMonthDay(value, locale) {
  if (!value || value.length !== 8) return ''; //  yyyyMMdd 
  const year = value.slice(0, 4);
  const month = value.slice(4, 6);
  const day = value.slice(6, 8);

  if (locale === 'ja') {
    return `${year}${month}${day}`; // 
  } else {
    const date = new Date(year, month - 1, day);
    return date.toLocaleString('en-US', { year: 'numeric', month: 'long', day: 'numeric' }); // 
  }
}

export function formatYen(value, locale) {
  if (!value || isNaN(value)) return "";

  const num = parseInt(value, 10);

  if (locale === "ja") {
    return `${Math.round(num / 10000)}`;
  } else {
    return `${(num / 1000000).toFixed(2)} M`;
  }
}

