      aboutEscore.value = escore[`about_${locale.value}`];
    };

    const getloaclStoreageInfo = () => {
      // 
      const info = JSON.parse(localStorage.getItem("homeContactInfo"));
      contactInfo.value.tel = info.tel;
      contactInfo.value.fax = info.fax;
      if (locale.value === "ja") {
        contactInfo.value.time = info.time_ja;
      } else {
        contactInfo.value.time = info.time_en;
      }
      // 
      const savedData = localStorage.getItem("contactInfo");
      if (savedData) {
        sendContactInfo.value = JSON.parse(savedData);
      }
    };

    const goback = () => {
      router.back(); // 
    };
    const showImage = (img, idx) => {
      index.value = idx;
      lightboxfDisp.value = true;
    };
    const callPhone = () => {
      window.location.href = "tel:" + contactInfo.value.tel;
    };
    const addLineFriend = () => {
      window.location.href = "line://ti/p/" + contactInfo.value.line;
    };

    const gotoReview = () => {
      if (isEmpty(sendContactInfo.value.name)) {
        getErrorMessage(t("contact-name") + t("error-required"));
        return;
      }
      if (isEmpty(sendContactInfo.value.mail)) {
        getErrorMessage(t("contact-email") + t("error-required"));
        return;
      }
      if (isEmpty(sendContactInfo.value.content)) {
        getErrorMessage(t("contact-content") + t("error-required"));
        return;
      }
      if (!validateEmail(sendContactInfo.value.mail)) {
        getErrorMessage(t("email-invalid"));
        return;
      }

      var path = "/reviewContactContent";
      var query = sendContactInfo.value;
      //  LocalStorage
      localStorage.setItem(
        "contactInfo",
        JSON.stringify(sendContactInfo.value)
      );
      router.push({ path, query });
    };
    const getErrorMessage = (message) => {
      ElMessage({
        message: message,
        type: "warning",
      });
    };
    const gotoList = () =>{
      router.back(); // 
    }
    onMounted(() => {
      getCarInfo();
      getAboutInfo();
      getloaclStoreageInfo();
    });
    watch(locale, () => {
      getCarInfo();
      getAboutInfo();
      getloaclStoreageInfo();
    });

    return {
      carInfo,
      aboutCar,
      contactInfo,
      sendContactInfo,
      aboutEscore,
      lightboxfDisp,
      goback,
      index,
      showImage,
      callPhone,
      addLineFriend,
      gotoReview,
      gotoList
    };
  },
};
